import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { TypeOfDocument } from '@models';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiService, SweetAlertService } from '@services';
import { FormListComponent } from '@fragments';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-type-of-document',
  templateUrl: './type-of-document.component.html',
  styleUrls: ['./type-of-document.component.scss']
})
export class TypeOfDocumentComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<TypeOfDocument>;
  displayedColumns: string[] = ['typeOfDocumentDesc', 'dateOfLaunchEnable', 'expiresOnEnable', 'basedOnEnable', 'variantEnable', 'remarksEnable', 'inputEnable', 'status'];
  
  typeOfDocument = new TypeOfDocument();
  typeOfDocumentList = [];
  statusList = [...STATUSES];

  isChecked = false;

  typeOfDocumentForm: FormGroup;
  hasInput = false;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.typeOfDocument).subscribe((res: any) => {
      this.typeOfDocumentList = res.responseData.data;
      this.dataSource = new MatTableDataSource<TypeOfDocument>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.typeOfDocument, this.typeOfDocument).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  getTypeOfDocument(): void {
    this.apiService.findById(ENDPOINTS.typeOfDocument, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.typeOfDocument = res.responseData.data;
      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.typeOfDocument, this.typeOfDocument).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setCheckboxValue() {
    this.isChecked = !this.isChecked;
  }

  setFormValidator(): void {
    this.typeOfDocument.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfDocumentForm = this.formBuilder.group({
      description: ['', Validators.required],
      dateOfLaunchEnable: [''],
      expiresOnEnable: [''],
      basedOnEnable: [''],
      variantEnable: [''],
      remarksEnable: [''],
      inputEnable: [''],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.typeOfDocument = new TypeOfDocument();
    this.typeOfDocument.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfDocumentForm.markAsUntouched();
    this.typeOfDocumentForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this type of document?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.typeOfDocument, this.typeOfDocument.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Type of document successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
